MeteorToys = Package["meteortoys:toykit"].MeteorToys;

Template.MeteorToys_accounts_content.events({
	'click .MeteorToys_row': function () {
		MeteorToys.set("MeteorToys_impersonate", false);
		MeteorToys.set("MeteorToys_impersonate", this.userID);
		MeteorToys.set("MeteorToys_current", null);
		Package["meteortoys:toykit"].ToyKit.set("focus");
	},
});

Template.MeteorToys_accounts_header.events({
	'click .MeteorToys_sub_button': function () {
		Meteor.connection.setUserId();
	}
});
Template.MeteorToys_accounts_content.helpers({
	account: function () {
		return Package["meteortoys:toykit"].MeteorToysData.Impersonate.find({}, {sort: {date: -1}});
	}
});

Tracker.autorun(function () {
	
	userId = MeteorToys.get("MeteorToys_impersonate");
	
	if (userId) {
		Meteor.call("MeteorToys_i", userId, function (e,r) { 
			if (!e) {
				Meteor.connection.setUserId(userId);
				MeteorToys.closeToy();  
			}
		});
	}
});

Accounts.onLogin(function () {
	window.setTimeout(function() {
		Meteor.call("MeteorToys_ia", function (e, r) {
		});
	}, 2000);
});

